@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.icon-container{
  text-align: center;
  margin: auto;
}
/* Styling the container for today's conditions */
.main-container {
  width: 100%;
  min-height: 30vh;
  max-height: 40vh;
  margin: 3% auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

/* Styling the title for today's conditions */
.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.title {
  font-size: 1.5rem;
  line-height: 1rem;
  font-weight: 400;
  margin-bottom: 0;
}
.timezone {
  font-size: .75rem;
    line-height: 1rem;
  font-weight: 300;
  text-align: center;
}

/* Styling the low/high temps of today's conditions */
.today-low-high {
  display: flex;
  padding-top: .5rem;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.today-low-high p {
  font-size: .9rem;
  line-height: 1rem;
  font-weight: 300;
  margin: 0 1rem;
}

@media screen and (max-height: 895px) {
  .today-low-high p {
    font-size: .8rem;
    line-height: .9rem;
    font-weight: 200;
  }
  .title {
    font-size: 1.2rem;
    line-height: .9rem;
    font-weight: 300;
  }
  .timezone {
    font-size: .6rem;
    line-height: .9rem;
    font-weight: 200;
  }
}

.forecast-card{
  text-align: center;
  margin: 0 0.5rem;
}
/* Potential background colors */
/* background-color: rgba(173, 216, 230, 0.5); */ 
/* background-color: rgba(107, 165, 255, 0.5); */
/* background-color: rgba(105, 105, 105, 0.3); */
.forecast-container {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  border-radius: 1em;
  width: 90%;
  min-height: 10vh;
  max-height: 18vh;
  margin: 0 auto 3%;
  overflow-x: scroll;
  overflow-y: hidden;
  background-color: rgba(40, 86, 163, 0.3);  
  /* Add padding-right equal to width of scrollbar */
  padding-right: 10px;
}

.forecast-container:hover {
  overflow-x: scroll;
}

.forecast-container::-webkit-scrollbar {
  width: 8px;
  background-color: rgba(0, 0, 0, 0.1);
}

.forecast-container::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}

.forecast-container:hover::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5);
}
.table-forecast {
  width: 90%;
  margin: 0 auto 1%;
  min-height: 25vh;
  max-height: 28vh;
  box-sizing: border-box;
  position: relative; /* Add position relative to the parent element */
  overflow-x: auto; /* Add horizontal scroll */
  overflow-y: hidden; /* Hide vertical scroll */
}

.table-margin {
  pointer-events: none; /* Ignore pointer events */
}

.table-forecast:hover > .table-margin {
  pointer-events: auto; /* Allow pointer events for the child element */
}

.loading-container{
  text-align: center;
}

.no-border{
  border: none;
}
html, body, #root {
  height: 100vh;
}

html, body {
  background: linear-gradient(to bottom, rgba(135, 206, 235, 0.6),  rgba(40, 86, 163, 0.8));
  /* background: linear-gradient(to bottom, rgba(192, 19∂2, 192, 0.7), rgba(105, 105, 105, 0.7)); */
  font-family: 'Roboto', sans-serif;
  color: white;
}
